<template>
	<div class="contentL">
		<!-- <div class="detailsPageHead">
			<img src="../images/arrowright.png" @click="history"/>
			<span>{{ title }}</span>
		</div> -->
		<div v-wechat-title="this.title"></div>
		<div class="wrap">
			<div class="d" v-for="(item,index) in list" @click='into(index)'>
				<img :src="item.picUrl" alt="">
				<span class="name">{{ item.pname }}</span>
				<div class="details">
					<span>{{ item.pmessage }}</span>
					<span class="rate" v-show="show">日利率：<b>{{ item.pinterest }}%</b></span>
				</div>
				<div class="fee">
					<span v-for="item in ptag">{{ item }}</span>
					<div class="countNum">申请人数：<b>{{ item.pBorrowNum }}人</b></div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import { getShowByTypeList, getUser } from '../api/api'
	import { getStore } from '../config/mUtils'
	export default {
		data() {
			return {
				list: [],
				ptag: null,
				title: null,
				show: true
			}
		},
		methods: {
			history() {
				this.$router.push({path: '/index'})
			},
			init() {
				var type = getStore('sortId')
				if (type === '32') {
					this.show = false;
				}
				this.title = getStore('title')
				let params = {
					type: type
				}
				getShowByTypeList(params).then(res => {
					if (res.code === 200) {
						this.list = res.data
						this.ptag = res.data[0].ptag.split('|')
					}
				})
			},
			into(index) {
				// setStore('id', this.list[index].id)
				// setStore('pcode', this.list[index].pcode)
			 //    this.$router.push({path: '/detailsPage', query: {distail: 3}})
			 var userId = getStore('userId')
    			let params = {
    				userId: userId
    			}
    			getUser(params).then(res => {
    				if (res.code === 200) {
    					parent.location.href = this.list[index].phttp
    				} else {
    					this.$router.push({path: '/login', query: {distail: 4}})
    				}
    			})
			}
		},
		computed: {

		},
		mounted() {
  			this.init()
  		}
	}
</script>
<style lang="scss" rel="stylesheet/scss">
@import './../common/sass/mixin.scss';
.contentL {
	background: #f2f3f2;
	height: 100%;
	z-index: 11;
	flex: 1;
	-webkit-flex: 1;

	.detailsPageHead {
		width: 100%;
		background: white;
		position: relative;
		height: 0.8rem;
		line-height: 0.8rem;
		
		img {
			transform: rotateZ(180deg);
			vertical-align: middle;
			width: 0.15rem;
			margin-left: 0.2rem;
		}
		span:nth-child(2) {
			vertical-align: middle;
			font-size: 0.25rem;
			color: black;
			display: inline-block;
			width: 90%;
			text-align: center;
		}
	}
	.wrap {
		padding: 0 0.3rem;
		background: #fff;
		z-index: 11;
	}
	.d {
		display: inline-block;
		width: 100%;
		height: 1.4rem;
		border-bottom: 0.01rem solid #bbb;
		background: #fff;
		position: relative;

		span {
			font-size: 0.22rem;
			color: #666666;
		}

		.name {
			font-size: 0.28rem;
			position: absolute;
			top: 0.2rem;
			left: 1.3rem;
			color: #333;
		}
		.details {
			position: absolute;
			top: 0.6rem;
			left: 1.3rem;
			right: 0;
			color: #999;

			span {
				display: inline-block;
				box-sizing: border-box;
				-webkit-box-sizing: border-box;
				-moz-box-sizing: border-box;
				-ms-box-sizing: border-box;
				max-width: 3rem;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
			.rate {
				float: right;
				b {
					color: #f48d24;
				}
			}
		}

		img:nth-child(1) {
			width: 1rem;
			height: 1rem;
			position: absolute;
			left: 0rem;
			top: 0.2rem;
			border-radius: 0.1rem;
		}
		img:nth-child(5) {
			position: absolute;
			right: 0;
			top: 0;
			width: 0.72rem;
		}
		.fee {
			position: absolute;
			top: 0.95rem;
			left: 1.3rem;
			right: 0;
			color: #aaaaaa;
			text-align: left;
			font-size: 0.2rem;
			span {
				display: inline-block;
				height: 0.3rem;
				line-height: 0.3rem
			}
			.countNum {
				position: absolute;
				right: 0rem;
				top: 0.06rem;
				font-size: 0.22rem;
				b {
					color: #d64a4d;
				}
			}
			span:nth-child(1) {
				border: 0.01rem solid #d64a4d;
				// padding: 0.05rem 0.1rem;
				padding: 0 0.1rem;
				border-radius: 0.05rem;
				margin-right: 0.1rem; 
			}
			span:nth-child(2) {
				background: #d64a4d;
				color: #fff;
				// padding: 0.05rem 0.15rem;
				padding: 0 0.1rem;
				border-radius: 0.05rem;
				border: 0.01rem solid #d64a4d;
			}
		}
	}
	.d:last-child {
		margin-bottom: 0.2rem;
	}
}
</style>



